                      <i class="q-icon las la-map-marked-alt"></i>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-5 col-lg-5">
                    <div class="text-primary">
                      {{ address ?? "--------" }},
                      {{ number ?? "---"
                      }}{{ complement ? ", " + complement : "" }}
                    </div>
                    <div class="text-primary">
                      Bairro: {{ district ? district : "--------" }}
                    </div>
