                      {{ city ?? "--------" }} <br/> CEP:
                      {{ zipcode ?? "--------" }}
                    </div>
                  </div>
                </q-card-section>

                <q-card-actions class="q-pt-lg">
                  <q-btn
                    v-if="phone"
