<style>
</style>

<script>
import { useQuasar } from "quasar";
import { ref } from "vue";
import Header from "../../components/Header.vue";

const today = new Date();


const setup = {
  //origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_CLIENTS
};

export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {
    const $q = useQuasar();
    const submitting = ref(false);
    const expanded = ref(false);

    return {
      setup: setup,
      dateSelect: today,
      data: null,
      client: null,
      peticon: 'paw',
      pettype: '',
      date_birth: null,
      status: '',
      expanded,
      submitting,
      pageTitle: "Cliente",
      pageSubTitle: "Pessoa autorizada",
      showNotif(msg, color, icon) {
        $q.notify({
          message: msg,
          color: color,
          icon: icon,
        });
      },
    };
  },
  data() {
    return {
      id: null,      
      name: null,
      rg: null,
      cpf: null,
      email: null,
      phone: null,
      phone2: null,
      address: null,
      number: null,
      complement: null,
      district: null,
      city: null,
      zipcode: null,      
      othername: null,
      otherphone: null,
      animais: [],
      pets: []
    };
  },
  methods: {

    async getClient() {
      //e.preventDefault();
      const req = await fetch(this.setup.url +'/'+ this.$route.params.id);
      this.jsondata = await req.json();
      this.client = this.jsondata.data.client;
      this.pets = this.jsondata.data.pets;



        (this.id = this.client.id),
        (this.name = this.client.name),
        (this.rg = this.client.rg),
        (this.cpf = this.client.cpf.replace(/(?<=\d\d)\d(?=\d{2})/g, "*")),
        (this.email = this.client.email),
        (this.phone = this.client.phone),
        (this.phone2 = this.client.phone2),
        (this.address = this.client.address),
        (this.number = this.client.number),
        (this.complement = this.client.complement),
        (this.district = this.client.district),
        (this.city = this.client.city),
        (this.zipcode = this.client.zipcode),
        (this.animais = this.client.animais),
        (this.othername = this.client.othername),
        (this.otherphone = this.client.otherphone);

    },
    async getBreedIcon(type){
      switch (type) {
        case 'feline': this.peticon = 'cat'
        break
        case 'canine': this.peticon = 'dog'
        break
        default: this.peticon = 'paw'
        }
    },
    async getBreedTypeName(type){
      switch (type) {
        case 'feline': this.pettype = 'Felino'
        break
        case 'canine': this.pettype = 'Canino'
        break
        default: this.pettype = 'Outros'
        }
    },
    async getWeight(weight){
      switch (weight) {
        case 'P': this.weight = '-10kg'
        break
        case 'M': this.weight = '+10kg'
        break
        case 'G': this.weight = '+20kg'
        break
        default: this.weight = '-10kg'
        }
    },
    async getdateformat(bdate){
      console.log(bdate)
      const options = { /*weekday: 'long',*/ year: 'numeric', month: 'long', day: 'numeric' };
      const today  = new Date(bdate);
      this.date_birth = today.toLocaleDateString("pt-BR", options)
    },
    async getstatus(status){
      switch (status) {
        case 'delete': this.status = 'Excluído'
        break
        case 'obito': this.status = 'Obito'
        break
        case 'interned': this.status = 'Internado'
        break
        case 'high': this.status = 'Alta'
        break
        case 'transferred': this.status = 'Transferido'
        break
        case 'comeback': this.status = 'Retorno'
        break
        default: this.status = 'Outros'
        }
    }
  },

  mounted() {
    this.getClient();
  },
};
</script>
